export class Node {
  constructor(
    id,                           //  ID
    div_type = "N",               // DIV  (None / Col / Row)
    node_type = "C",              //  (Disable / Parent / Child )
    node_text = "",               // Body Text
    inset_top = 0,
    inset_right = 0,
    inset_bottom = 0,
    inset_left = 0,
    ratio = 50,

    p_id = null,
    left = null,
    right = null
  ) {
    this.id = id;
    this.div_type = div_type;
    this.node_type = node_type;
    this.node_text = node_text;

    this.inset_top = inset_top;
    this.inset_right = inset_right;
    this.inset_bottom = inset_bottom;
    this.inset_left = inset_left;
    this.ratio = ratio;

    this.p_id = p_id;
    this.left = left;
    this.right = right;
  }
}

